$text-size: 15px
$body-color: #151515
$body-text-color: hsl(0, 0%, 80%)
$input-text-color: #fff
$button-text-color: #fff
$button-lg-1: #3B33BD
$button-lg-2: #C161F9
$button-secondary-color: #2B2B2B
$header-text-color: #FFF

@font-face 
    font-family: "Figtree"
    src: url("fonts/Figtree-Regular.ttf") format("truetype")
    font-weight: normal
    font-style: normal

@font-face 
    font-family: "Figtree"
    src: url("fonts/Figtree-Bold.ttf") format("truetype")
    font-weight: bold
    font-style: normal

@font-face 
    font-family: "Figtree"
    src: url("fonts/Figtree-Italic.ttf") format("truetype")
    font-weight: normal
    font-style: italic

body, html
    margin: 0
    padding: 0
    line-height: 1.5
    font-family: "Figtree", Tahoma
    font-size: $text-size
    color: $body-text-color
